// ** React Imports
import { Navigate } from 'react-router-dom'
import { Suspense, useContext } from 'react'

// ** Context Imports
import { AbilityContext } from '@src/utility/context/Can'

const PrivateRoute = ({ children, route }) => {
  // ** Hooks & Vars
  const ability = useContext(AbilityContext)
  const user = JSON.parse(localStorage.getItem('userData'))
  const tempArr = ['reports', 'attendance', 'invoice', 'task', 'client', 'chat', 'dashboard', 'filemanager', 'profile', 'time-sheet']
  if (route) {
    let action = null
    let resource = null
    let restrictedRoute = false

    action = route?.action
    resource = route?.resource
    restrictedRoute = route?.meta?.restricted ?? false

    if (!user) {
      return <Navigate to='/login' />
    }
    if (user && restrictedRoute) {
      return <Navigate to='/' />
    }
    if (user && (!ability.can(action || 'read', resource) && tempArr.indexOf(resource) === -1)) {
      return <Navigate to='/misc/not-authorized' />
    }
    if (!user && !ability.can(action || 'read', resource)) {
      return <Navigate to='/misc/not-authorized' replace />
    }
  }

  return <Suspense fallback={null}>{children}</Suspense>
}

export default PrivateRoute
